const Main = resolve => require(['@/layout/index'], resolve);

// 作为Main组件的子页面
export const appRouter = {
    path: '/',
    name: 'entry',
    redirect: 'login',
    component: Main,
    children: [
        {
            path: '/log',
            name: 'log',
            meta: {
                title: '日志查询',
                icon: 'el-icon-s-home',
                isMenu: true
            },
            component: () => import('@/views/log'),
        }
    ]
};

// 作为其他组件的子页面   例如：登录页
export const otherRouter = [{
    path: '/login',
    name: 'login',
    component: () => import('@/views/login/index')
}, {
    path: '/404',
    name: 'error-404',
    meta: {
        title: '404-页面不存在'
    },
    component: resolve => {
        require(['@/views/404.vue'], resolve);
    }
}];

// 所有上面定义的路由都要写在下面的routers里
export const routers = [appRouter, ...otherRouter];
