import Vue from 'vue';
import VueRouter from 'vue-router';
import {
    routers,
} from './router';
import store from '../store';

Vue.use(VueRouter);
// 路由配置
const RouterConfig = {
     // mode: 'history',
    routes: routers,
};
const router = new VueRouter(RouterConfig);
router.beforeEach((to, from, next) => {
    let token = sessionStorage.getItem('status');
    if (to.name === 'login') {
        sessionStorage.removeItem('status');
        sessionStorage.removeItem('userInfo');
        next();
    }
    else if (token) {
        store.commit('updateMenu');
        if (store.state.app.accessName.indexOf(to.name) === -1 && to.name !== 'error-404' && to.name !== 'role_detail') {
            next({
                replace: true,
                name: 'error-404'
            });
        } else {
            next(true);
        }
         next();
    }
    else {
        next({
            name: 'login'
        });
    }
});



