import {
    appRouter
} from "@/router/router";

import axios from "../../config/axios.js";

const user = {
    state: {
        bOperateMenu: false,
        aMenuList: [],
        accessName: [],
        aPermList: [

            {
                role: "普通用户",
                powerType: 0,
                menu: [

                    {
                        name: "log"
                    },
                ]
            },
            {
                role: "超级管理员",
                powerType: 1,
                menu: [
                    {
                        name: "log"
                    }
                ]
            },
        ]
    },
    mutations: {
        // 更新menu菜单
        updateMenu(state) {
            let curRole = {
                menu: []
            };
            let userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
            let powerType = userInfo.powerType;

            if (powerType > 1) {
                let temp = {
                    role: "",
                    powerType: powerType,
                    menu: []
                }
                let menuList = userInfo.menuList;
                if (menuList != null) {
                    menuList.forEach((v) => {
                        if (v != "role_detail") {
                            temp.menu.push({ name: v });
                        }
                    })
                }
                state.aPermList.push(temp);
            }

            state.aPermList.forEach(item => {
                if (item.powerType == powerType) {
                    curRole = item;
                }
            });
            appRouter.children.forEach(item => {
                let {
                    meta
                } = item;
                curRole.menu.forEach(ele => {
                    if (ele.name == item.name) {
                        ele.title = meta.title;
                        ele.isMenu = meta.isMenu;
                    }
                    if (item.meta.parentMenu != null) {
                        if (ele.name == item.meta.parentMenu) {
                            if (ele.clildrenMenu == null)
                                ele.clildrenMenu = [];
                            let isExist = false;
                            for (let i = 0; i < ele.clildrenMenu.length; i++) {
                                if (JSON.stringify(ele.clildrenMenu[i]) === JSON.stringify({ name: item.name, title: meta.title })) {
                                    isExist = true;
                                    break;
                                }
                            }
                            if (!isExist) {
                                ele.clildrenMenu.push({ name: item.name, title: meta.title });
                            }
                        }
                    }
                });
            });
            let newName = [];
            curRole.menu.forEach(item => {
                newName.push(item.name);
            });

            state.aMenuList = curRole.menu;
            state.accessName = newName;
            state.bOperateMenu = true;
        }
    },

    actions: {}
};
export default user;
